* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #1a1a1a;
  background: linear-gradient(120deg, #e0f7fa, #f8faff);
  font-family: Inter, Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
}

.portfolio {
  display: flex;
}

a {
  color: #0070f3;
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #0051a3;
}

.container {
  min-height: 100vh;
  display: flex;
}

.sidebar {
  text-align: center;
  background-color: #fefefe;
  border-right: 1px solid #e0e0e0;
  width: 250px;
  height: 100vh;
  padding: 40px 20px;
  position: sticky;
  top: 0;
}

.sidebar .headshot {
  object-fit: cover;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
}

.sidebar h1 {
  margin-bottom: 5px;
  font-size: 1.8rem;
}

.sidebar p {
  color: #333;
  margin-bottom: 30px;
  font-size: .95rem;
}

.sidebar nav ul {
  flex-direction: column;
  gap: 15px;
  list-style: none;
  display: flex;
}

.sidebar nav ul li a {
  color: #000;
  font-size: 1rem;
  font-weight: 500;
}

.content {
  flex: 1;
  max-width: 900px;
  margin: 0 auto;
  padding: 60px 40px;
}

section {
  margin-bottom: 60px;
}

section h2 {
  color: #1a1a1a;
  border-bottom: 2px solid #0077b6;
  margin-bottom: 20px;
  padding-bottom: 5px;
  font-size: 2rem;
  display: inline-block;
}

section p, section ul {
  color: #333;
  margin-bottom: 20px;
  font-size: 1rem;
}

section ul {
  padding-left: 20px;
}

section ul li {
  margin-bottom: 10px;
}

.card {
  background: #fefefe;
  border-radius: 12px;
  min-width: 280px;
  margin: 10px;
  padding: 16px 20px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 2px 6px #0000000f;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #00000014;
}

.card p {
  color: #475569;
  font-size: .95rem;
}

.card h3 {
  margin-bottom: 8px;
  font-size: 1.1rem;
  font-weight: 600;
}

.about-me-icons {
  justify-content: center;
  display: flex;
}

#projects {
  flex-wrap: wrap;
  gap: 20px;
}

.project-cards-container {
  display: flex;
}

footer {
  text-align: center;
  color: #777;
  border-top: 1px solid #e0e0e0;
  padding: 30px 0;
  font-size: .9rem;
}

@media (width <= 1024px) {
  .container {
    flex-direction: column;
  }

  .sidebar {
    border-bottom: 1px solid #e0e0e0;
    border-right: none;
    width: 100%;
    padding: 20px;
  }

  .content {
    padding: 40px 20px;
  }

  #projects {
    justify-content: center;
  }
}

.timeline-section {
  max-width: 800px;
  margin: 0 auto;
}

.section-title {
  text-align: center;
  color: #1a1a1a;
  margin-bottom: 50px;
  font-size: 2rem;
  font-weight: 600;
}

.timeline {
  border-left: 2px solid #e2e8f0;
  margin-left: 20px;
  padding-left: 30px;
  position: relative;
}

.timeline-item {
  margin-bottom: 40px;
  position: relative;
}

.timeline-dot {
  background-color: #0077b6;
  border: 3px solid #f9fafb;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  position: absolute;
  top: 6px;
  left: -10px;
}

.timeline-date {
  color: #1a1a1a;
  margin: 10px;
  font-size: .9rem;
}

.info-icon {
  color: #0077b6;
  margin: 10px;
}

@media (width <= 600px) {
  .timeline {
    margin-left: 10px;
    padding-left: 20px;
  }

  .timeline-content {
    padding: 14px;
  }

  .section-title {
    font-size: 1.6rem;
  }
}

.chips-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  padding: 8px;
  display: flex;
}

.chip {
  color: #1565c0;
  background: #e3f2fd;
  border-radius: 16px;
  align-items: center;
  padding: 6px 12px;
  font-size: 14px;
  display: flex;
}

.project {
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  display: flex;
}
